.Spinner {
  display: inline-block;
  width: 30px;
  height: 30px;
}
.Spinner:after {
  content: " ";
  display: block;
  width: 24px;
  height: 24px;
  margin: 3px;
  border-radius: 50%;
  border: 3px solid #8a91ab;
  border-color: #8a91ab transparent #8a91ab transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
